
import { defineComponent } from "vue";
import { FileSize } from "@/shared/services/FileSizeService";
import { ICM20100112_RES, ICM20100112_REQ_BODY, ContentsList, FileInfo } from "@/shared/type/ICM20100112.type";
import { ICM20100121_RES, ICM20100121_REQ_BODY } from "@/shared/type/ICM20100121.type";
import { ICM20100141_RES, ICM20100141_REQ_BODY } from "@/shared/type/ICM20100141.type";
import { ICM20100131_RES, ICM20100131_REQ_BODY } from "@/shared/type/ICM20100131.type";
import { APM20200113_RES } from "@/shared/type/APM20200113.type";
import { ModifyCardType } from "@/shared/components/ModifyCard/ModifyCard.type"
import { AttachInfoList } from "@/shared/type/ICM21100112.type";
import { modalController } from "@ionic/vue";
import { screenMode } from "@/shared/type/Approval.type";
import { FILE, FILE_TYPE } from "@/shared/constants/common.const";
import baseTab from "@/shared/components/Tab/BaseTab.vue";
import basePan from "@/shared/components/Tab/BaseTabPan.vue";
import ModifyCard from "@/shared/components/ModifyCard/ModifyCard.vue";
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue";
import DialogService from "@/shared/services/DialogService"
import TextEditor from '@/shared/components/TextEditor/TextEditor.vue'

const fileSize = new FileSize()

export default defineComponent({
    name: "ICM201002SC01",
    components: {
        baseTab,
        basePan,
        DialogService,
        ModifyCard,
        BreadCrumb,
        SelectOption,
        TextEditor
    },

    data() {
        return {
            headersIcon: [
                { key: "En", name: "common.english" },
                { key: "Km", name: "common.khmer" },
                { key: "Ko", name: "common.korean" },
                // { key: "Ja", name: "common.japanese" },
                // { key: "Zh", name: "common.chinese" },
            ],
            newEventDetailsLangDefaultValue: [
                { languageCode: "common.english", title: "", contentDesc: "", contentHtml: "" },
                { languageCode: "common.khmer", title: "", contentDesc: "", contentHtml: "" },
                { languageCode: "common.korean", title: "", contentDesc: "", contentHtml: "" },
                { languageCode: "common.japanese", title: "", contentDesc: "", contentHtml: "" },
                { languageCode: "common.chinese", title: "", contentDesc: "", contentHtml: "" },
            ] as ContentsList[],
            newEventDetail: {
                displayYN: "",
                newsEventID: "",
                attachInfoList: [] as AttachInfoList[],
                contentsList: [] as ContentsList[],
                newsEventCategoryCode: "",
                statusCode: "",
            },
            modifyCard: {} as ModifyCardType,
            isEdit: true,
            fileInfo: {} as FileInfo,
            isInValidInput: false,
            getFile: null,
            getImage: null,
            isFileEmty: false,
            isImageEmty: false,
            operationMode: 'normal' as screenMode,
            approvalRes: {} as any,
        }
    },

    async mounted() {
        if (this.approvalType === '') {
            if (this.$route.query.type === "register") {
                this.$util.editableContent(true)
                this.isEdit = false
            } else {
                this.$util.editableContent(false)
                this.requestNewEventDetail()
            }
        } else {
            await this.$approval.getAuthorizationDetail().then(async (authDetail: APM20200113_RES) => {
                let authDetailParsed = JSON.parse(authDetail.body.requestContent)
                this.approvalRes = authDetail.body
                this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal'
                this.$util.editableContent(false);
                if (this.$util.checkServiceIdType(authDetail.body.serviceID) == 'delete') {
                    const reqBody = { newsEventID: Number(authDetailParsed.newsEventID) } as ICM20100112_REQ_BODY;
                    const res = await this.$request.getRequest("ICM20100112", reqBody, false) as ICM20100112_RES;
                    await this.assignValueToTab(this.convertCommonCode(res?.body.contentsList))
                    this.assignValueToAttachInfoList(res?.body.attachInfoList)
                    this.newEventDetail = {
                        displayYN: res?.body.displayYN,
                        newsEventID: res?.body.newsEventID,
                        attachInfoList: this.newEventDetail.attachInfoList,
                        contentsList: res?.body.contentsList,
                        newsEventCategoryCode: res?.body.newsEventCategoryCode,
                        statusCode: res?.body.statusCode,
                    };
                    Promise.all([this.requestFileDetail(FILE_TYPE.FILE), this.requestFileDetail(FILE_TYPE.IMAGE)])
                } else {
                    await this.assignValueToTab(this.convertCommonCode(authDetailParsed.contentsList))
                    this.assignValueToAttachInfoList(authDetailParsed.attachInfoList)
                    this.newEventDetail = {
                        displayYN: authDetailParsed.displayYN,
                        newsEventID: authDetailParsed.newsEventID,
                        attachInfoList: authDetailParsed.attachInfoList,
                        contentsList: authDetailParsed.contentsList,
                        newsEventCategoryCode: authDetailParsed.newsEventCategoryCode,
                        statusCode: authDetailParsed.statusCode
                    };
                    await Promise.all([this.requestFileDetail(FILE_TYPE.FILE), this.requestFileDetail(FILE_TYPE.IMAGE)])
                }
            })
        }
    },

    computed: {
        approvalType(): string {
            return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
        },
        displayYN(): any {
            return this.$store.state.CommonCodeModule.commonCode.YnType
        },
        NewsEventCategoryCode(): any {
            return this.$store.state.CommonCodeModule.commonCode.NewsEventCategoryCode
        },
        btnDelete() {
            if (this.$route.query.type == 'edit' && this.isEdit) {
                return true
            }
            return false
        },
        btnList() {
            if (this.$route.query.type == 'edit' && this.isEdit) {
                return true
            }
            return false
        },
        btnCancel() {
            if (!this.isEdit) {
                return true
            }
            return false
        },
        btnEdit() {
            if (this.$route.query.type == 'edit' && this.isEdit) {
                return true
            }
            return false
        },
        btnSave() {
            if ((this.$route.query.type == 'edit' && !this.isEdit) || (this.$route.query.type == 'register')) {
                return true
            }
            return false
        },
        deleted() {
            if (this.newEventDetail.statusCode === '09') {
                return true
            }
            return false
        }
    },

    watch: {
        newEventDetailsLangDefaultValue: {
            handler(val) {
                val.forEach((item: { contentDesc: string; contentHtml: string; }) => {
                    item.contentDesc = this.$validate.formatHtmlToString(item.contentHtml)
                })
            },
            deep: true
        }
    },

    methods: {
        async requestNewEventDetail() {
            const reqBody = { newsEventID: Number(this.$route.query.newsEventID) } as ICM20100112_REQ_BODY;
            const res = await this.$request.getRequest("ICM20100112", reqBody, false) as ICM20100112_RES;
            await this.assignValueToTab(this.convertCommonCode(res?.body.contentsList))
            this.assignValueToAttachInfoList(res?.body.attachInfoList)
            this.newEventDetail = {
                displayYN: res?.body.displayYN,
                newsEventID: res?.body.newsEventID,
                contentsList: res?.body.contentsList,
                attachInfoList: this.newEventDetail.attachInfoList,
                newsEventCategoryCode: res?.body.newsEventCategoryCode,
                statusCode: res?.body.statusCode,
            }
            this.modifyCard = {
                registerTellerId: res?.body.firstRegisterTellerID,
                registerTellerName: res?.body.firstRegisterTellerName,
                registerDate: res?.body.firstRegisterDate,
                registerTime: res?.body.firstRegisterTime,
                updatedTellerId: this.newEventDetail.statusCode === '09' ? "" : res?.body.lastChangeTellerID,
                updatedTellerName: this.newEventDetail.statusCode === '09' ? "" : res?.body.lastChangeTellerName,
                updatedDate: this.newEventDetail.statusCode === '09' ? "" : res?.body.lastChangeDate,
                updatedTime: this.newEventDetail.statusCode === '09' ? "" : res?.body.lastChangeTime,
                deleteTellerId: this.newEventDetail.statusCode === '09' ? res?.body.lastChangeTellerID : "",
                deleteTellerName: this.newEventDetail.statusCode === '09' ? res?.body.lastChangeTellerName : "",
                deleteDate: this.newEventDetail.statusCode === '09' ? res?.body.lastChangeDate : "",
                deleteTime: this.newEventDetail.statusCode === '09' ? res?.body.lastChangeTime : "",
            }
            Promise.all([this.requestFileDetail(FILE_TYPE.FILE), this.requestFileDetail(FILE_TYPE.IMAGE)])
        },

        async requestFileDetail(attachTypeCode: string) {
            for (let i = 0; i < this.newEventDetail.attachInfoList.length; i++) {
                if (this.newEventDetail.attachInfoList[i].attachTypeCode == attachTypeCode) {
                    const res = await this.$file.getFileInfo(this.newEventDetail.attachInfoList[i].fileId)
                    if (attachTypeCode == FILE_TYPE.FILE) {
                        this.fileInfo.fileName = res?.data.body.originalFileName
                        this.fileInfo.fileSize = res?.data.body.fileSizeString
                    } else {
                        this.fileInfo.imageName = res?.data.body.originalFileName
                        this.fileInfo.imgSize = res?.data.body.fileSizeString
                        const resImage = await this.$file.previewImage(this.newEventDetail.attachInfoList[i].fileId)
                        this.fileInfo.image = resImage
                    }
                }
            }
        },

        async handleRegister() {
            await this.handleFileRegister()
            .then(async (res) => {
                if(res === false) {
                    console.error('Error file server: please update without file!')
                } else {
                    const reqBody = {
                        newsEventCategoryCode: this.newEventDetail.newsEventCategoryCode,
                        displayYN: this.newEventDetail.displayYN,
                        contentsList: this.convertLangToCode(JSON.parse(JSON.stringify(this.newEventDetailsLangDefaultValue))),
                        attachInfoList: this.newEventDetail.attachInfoList
                    } as ICM20100121_REQ_BODY;
                    const res = await this.$request.getRequest("ICM20100121", reqBody, false) as ICM20100121_RES;

                    if (res.header.result) {
                        this.$router.push("ICM201001SC00");
                        this.$dialog.onOpen({
                            content: this.$t("message.register_success"),
                            dialogMode: 'Success',
                            callback: () => {
                                modalController.dismiss()
                            }
                        });
                    }
                }
            })
        },

        async handleUpdate() {
            await this.handleFileUpdate()
                .then(async (res) => {
                    if(res === false) {
                        console.error('Error file server: please register without file!')
                    } else {
                        const reqBody = {
                            newsEventCategoryCode: this.newEventDetail.newsEventCategoryCode,
                            displayYN: this.newEventDetail.displayYN,
                            contentsList: this.convertLangToCode(JSON.parse(JSON.stringify(this.newEventDetailsLangDefaultValue))),
                            attachInfoList: this.newEventDetail.attachInfoList,
                            newsEventID: Number(this.newEventDetail.newsEventID)
                        } as ICM20100131_REQ_BODY;
                        const res = await this.$request.getRequest("ICM20100131", reqBody, false) as ICM20100131_RES;
                        if (res.header.result) {
                            this.isEdit = true;
                            this.$util.editableContent(false);
                            this.$dialog.onOpen({
                                content: this.$t("message.update_success"),
                                dialogMode: 'Success',
                                callback: () => {
                                    this.requestNewEventDetail()
                                    modalController.dismiss()
                                }
                            });
                        }
                    }
                })
        },

        async handleDelete() {
            const reqBody = { newsEventID: Number(this.$route.query.newsEventID) } as ICM20100141_REQ_BODY;
            const res = await this.$request.getRequest("ICM20100141", reqBody, false) as ICM20100141_RES;
            // this.newEventDetail.attachInfoList.forEach((item) => {
            //   this.$file.fileDelete(item.fileId);
            // })
            if (res.header.result) {
                this.$router.push("ICM201001SC00");
                this.$dialog.onOpen({
                    content: this.$t("message.delete_success"),
                    dialogMode: 'Success',
                    callback: () => {
                        modalController.dismiss()
                    }
                });
            }
        },

        handleClickAction(buttonType: string) {
            switch (buttonType) {
                case "delete":
                    if (this.$route.query.type == "edit") {
                        this.$dialog.onOpen({
                            content: this.$t('message.are_you_sure_you_want_to_delete'),
                            dialogMode: "Question",
                            callback: () => {
                                this.handleDelete();
                                modalController.dismiss();
                            },
                        });
                    } else {
                        this.$router.push("ICM201001SC00");
                    }
                    break;
                case "cancel":
                    this.$dialog.onOpen({
                        content: `${this.$route.query.type === 'edit' ? this.$t("message.cancel_without_saving_changes") : this.$t('message.are_you_sure_you_want_to_cancel_registration?')}`,
                        dialogMode: "Question",
                        callback: () => {
                            if (this.$route.query.type == "edit") {
                                this.fileInfo.fileName = this.fileInfo.fileSize = this.fileInfo.imageName = this.fileInfo.imgSize = ''
                                this.requestNewEventDetail();
                                this.isEdit = true;
                                this.$util.editableContent(false);
                            } else {
                                this.$router.push("ICM201001SC00");
                            }
                            modalController.dismiss()
                        },
                    });
                    break;
                case "save":
                    var item: any = this.newEventDetailsLangDefaultValue.find((data) => data.languageCode === 'common.english');
                    if (item.contentDesc && item.title && this.NewsEventCategoryCode) {
                        this.$dialog.onOpen({
                            content: this.$t("message.would_you_like_to_save"),
                            dialogMode: "Question",
                            callback: () => {
                                if (this.$route.query.type == "edit") {
                                    this.handleUpdate();
                                } else {
                                    this.handleRegister();
                                }
                                modalController.dismiss()
                            },
                        });
                    } else {
                        this.isInValidInput = true;
                        this.$util.scrollToElement('inp_error')
                    }
                    break;
                case "edit":
                    this.isEdit = false;
                    this.$util.editableContent(true);
                    break;
                case 'list':
                    this.$router.push("ICM201001SC00")
                    break;
            }
        },

        convertCommonCode(listArr: ContentsList[]): ContentsList[] {
            const convertLangCode = listArr.map((item: ContentsList) => {
                item.languageCode = item.languageCode ? this.$codeConverter.convertValue("LanguageCodeTranslate", item.languageCode) : "";
                return item;
            });
            return convertLangCode;
        },

        convertLangToCode(listsArr: ContentsList[]): ContentsList[] {
            const data = listsArr.map((item: ContentsList) => {
                item.languageCode = this.$codeConverter.convertValue("LanguageCodeTranslate", item.languageCode, "value");
                return item;
            });
            return data;

            // Below algorithms will be crucial when you want to expose english tab to others tab which are empty

            // const filter = listsArr.find((item: ContentsList) => item.languageCode === 'common.english')
            // if (filter) {
            //   const data = listsArr.map((item: ContentsList) => {
            //     if (!item.contentDesc && !item.title) {
            //       item.contentDesc = filter.contentDesc
            //       item.contentHtml = filter.contentHtml
            //       item.title = filter.title
            //     }
            //     item.languageCode = this.$codeConverter.convertValue("LanguageCodeTranslate", item.languageCode, "value");
            //     return item;
            //   });
            //   return data;
            // }
        },

        handleUploadImage(event: any) {
            this.isImageEmty = false
            const accectFile = /(\.png)$/i;
            const files = event.target.files;
            const file = files[0];
            if (!accectFile.exec(file.name)) {
                this.$dialog.onOpen({
                    content: this.$t('message.only_png_file_can_be_inserted'),
                    dialogMode: "Warning",
                    callback: () => {
                        modalController.dismiss();
                    },
                });
            } else if (file.size > FILE.SIZE_2MB) {
                this.$dialog.onOpen({
                    content: this.$t('message.file_size_must_not_exceed_2MB'),
                    dialogMode: "Warning",
                    callback: () => {
                        modalController.dismiss();
                    },
                });
            } else {
                this.getImage = file;
                const size = fileSize.countFileSize(file.size);
                this.fileInfo.imgSize = size;
                const reader = new FileReader();
                reader.readAsDataURL(file);
                this.fileInfo.imageName = file.name;
                reader.onload = () => {
                    this.fileInfo.image = String(reader.result);
                };
            }
            const duplicatedFile = document.getElementById('btn02 btn_upload') as HTMLInputElement;
            duplicatedFile.value = ''
        },

        removeFile(attachTypeCode: string) {
            switch (attachTypeCode) {
                case FILE_TYPE.FILE:
                    this.isFileEmty = true
                    this.getFile = null
                    this.fileInfo.fileSize = this.fileInfo.fileName = '';
                    break;
                case FILE_TYPE.IMAGE:
                    this.isImageEmty = true
                    this.getImage = null
                    this.fileInfo.imageName = this.fileInfo.imgSize = '';
                    break;
            }
        },

        handleUploadFile(event: any) {
            this.isFileEmty = false
            const files = event.target.files;
            const file = files[0];
            if (file.size > FILE.SIZE_10MB) {
                this.$dialog.onOpen({
                    content: this.$t('message.file_size_must_not_exceed_10MB'),
                    dialogMode: "Warning",
                    callback: () => {
                        modalController.dismiss()
                    },
                });
            } else {
                this.getFile = file;
                const size = fileSize.countFileSize(file.size);
                this.fileInfo.fileSize = size;
                const reader = new FileReader();
                reader.readAsDataURL(file);
                this.fileInfo.fileName = file.name;
            }
            const duplicatedFile = document.getElementById('actual-btn') as HTMLInputElement;
            duplicatedFile.value = ''
        },

        isFileAlreadyExisted(fileContentType: FILE_TYPE.FILE | FILE_TYPE.IMAGE) {
            const isFileAlreadyExisted = this.newEventDetail.attachInfoList.some(obj => Object.values(obj).includes(fileContentType))
            return isFileAlreadyExisted
        },

        async handleFileUpdate() {
      if (this.getFile !== null && this.getImage !== null && !this.isFileEmty && !this.isImageEmty) {
        if (this.newEventDetail.attachInfoList) {
          this.newEventDetail.attachInfoList.forEach(async (item) => {
            await this.$file.updateFileStatus(item.fileId, FILE_TYPE.INACTIVE)
          })
        }
        this.newEventDetail.attachInfoList = []
        const res = await this.$file.uploadFile([this.getFile, this.getImage], "ICM");
        if (res) {
          for (let i = 0; i < res.data.body.fileList.length; i++) {
            if (res.data.body.fileList[i].keyName === "file1") {
              const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
              this.newEventDetail.attachInfoList.push({
                filePath: resFileInfo.data.body.filePath,
                attachTypeCode: FILE_TYPE.FILE,
                fileId: res.data.body.fileList[i].fileId
              })
            }
            if (res.data.body.fileList[i].keyName === "file2") {
              const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
              this.newEventDetail.attachInfoList.push({
                filePath: resFileInfo.data.body.filePath,
                attachTypeCode: FILE_TYPE.IMAGE,
                fileId: res.data.body.fileList[i].fileId
              })
            }
          }
        } else {
          return false
        }
      }
      if (this.getFile === null && this.isFileEmty) {
        for (let i = 0; i < this.newEventDetail.attachInfoList.length; i++) {
          if (this.newEventDetail.attachInfoList[i].attachTypeCode === FILE_TYPE.FILE) {
            const res = await this.$file.updateFileStatus(this.newEventDetail.attachInfoList[i].fileId, FILE_TYPE.INACTIVE)
            if (res) {
              const index = this.newEventDetail.attachInfoList.indexOf(this.newEventDetail.attachInfoList[i]);
              this.newEventDetail.attachInfoList.splice(index, 1);
            } else {
              return false
            }
          }
        }
      }
      if (this.getImage === null && this.isImageEmty) {
        for (let i = 0; i < this.newEventDetail.attachInfoList.length; i++) {
          if (this.newEventDetail.attachInfoList[i].attachTypeCode === FILE_TYPE.IMAGE) {
            const res = await this.$file.updateFileStatus(this.newEventDetail.attachInfoList[i].fileId, FILE_TYPE.INACTIVE)
            if (res) {
              const index = this.newEventDetail.attachInfoList.indexOf(this.newEventDetail.attachInfoList[i]);
              this.newEventDetail.attachInfoList.splice(index, 1);
            } else {
              return false
            }
          }
        }
      }
      if (this.getFile !== null && !this.isFileEmty) {
        if (this.newEventDetail.attachInfoList.length === 0) {
          const res = await this.$file.uploadFile([this.getFile], "ICM")
          if (res) {
            for (let i = 0; i < res.data.body.fileList.length; i++) {
              const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
              this.newEventDetail.attachInfoList.push({
                filePath: resFileInfo.data.body.filePath,
                attachTypeCode: FILE_TYPE.FILE,
                fileId: res.data.body.fileList[i].fileId
              })
            }
          } else {
            return false
          }
        } else if (this.newEventDetail.attachInfoList.length > 0) {
          if (this.isFileAlreadyExisted(FILE_TYPE.FILE)) {
            const res = await this.$file.uploadFile([this.getFile], "ICM")
            if (res) {
              for (let index = 0; index < this.newEventDetail.attachInfoList.length; index++) {
                if (this.newEventDetail.attachInfoList[index].attachTypeCode === FILE_TYPE.FILE) {
                  await this.$file.updateFileStatus(this.newEventDetail.attachInfoList[index].fileId, FILE_TYPE.INACTIVE);
                  for (let i = 0; i < res.data.body.fileList.length; i++) {
                    await this.$file.getFileInfo(res.data.body.fileList[i].fileId).then(v =>
                      Object.assign(this.newEventDetail.attachInfoList[index], { fileId: res.data.body.fileList[i].fileId, attachTypeCode: FILE_TYPE.FILE, filePath: v.data.body.filePath })
                    )
                  }
                }
              }
            } else {
              return false
            }
          } else {
            const res = await this.$file.uploadFile([this.getFile], "ICM")
            if (res) {
              for (let i = 0; i < res.data.body.fileList.length; i++) {
                const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
                this.newEventDetail.attachInfoList.push({
                  filePath: resFileInfo.data.body.filePath,
                  attachTypeCode: FILE_TYPE.FILE,
                  fileId: res.data.body.fileList[i].fileId
                })
              }
            } else {
              return false
            }
          }
        }
      }
      if (this.getImage !== null && !this.isImageEmty) {
        if (this.newEventDetail.attachInfoList.length === 0) {
          const res = await this.$file.uploadFile([this.getImage], "ICM")
          if (res) {
            for (let i = 0; i < res.data.body.fileList.length; i++) {
              const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
              this.newEventDetail.attachInfoList.push({
                filePath: resFileInfo.data.body.filePath,
                attachTypeCode: FILE_TYPE.IMAGE,
                fileId: res.data.body.fileList[i].fileId
              })
            }
          } else {
            return false
          }
        } else if (this.newEventDetail.attachInfoList.length > 0) {
          if (this.isFileAlreadyExisted(FILE_TYPE.IMAGE)) {
            const res = await this.$file.uploadFile([this.getImage], "ICM")
            if (res) {
              for (let index = 0; index < this.newEventDetail.attachInfoList.length; index++) {
                if (this.newEventDetail.attachInfoList[index].attachTypeCode === FILE_TYPE.IMAGE) {
                  await this.$file.updateFileStatus(this.newEventDetail.attachInfoList[index].fileId, FILE_TYPE.INACTIVE);
                  for (let i = 0; i < res.data.body.fileList.length; i++) {
                    await this.$file.getFileInfo(res.data.body.fileList[i].fileId).then(v =>
                      Object.assign(this.newEventDetail.attachInfoList[index], { fileId: res.data.body.fileList[i].fileId, attachTypeCode: FILE_TYPE.IMAGE, filePath: v.data.body.filePath })
                    )
                  }
                }
              }
            } else {
              return false
            }
          } else {
            const res = await this.$file.uploadFile([this.getImage], "ICM")
            if (res) {
              for (let i = 0; i < res.data.body.fileList.length; i++) {
                const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
                this.newEventDetail.attachInfoList.push({
                  filePath: resFileInfo.data.body.filePath,
                  attachTypeCode: FILE_TYPE.IMAGE,
                  fileId: res.data.body.fileList[i].fileId
                })
              }
            } else {
              return false
            }
          }
        }
      }
    },

    async handleFileRegister(): Promise<boolean | void> {
      if (this.getFile !== null && this.getImage !== null) {
        const res = await this.$file.uploadFile([this.getFile, this.getImage], "ICM");
        if (res) {
          for (let i = 0; i < res.data.body.fileList.length; i++) {
            if (res.data.body.fileList[i].keyName === "file1") {
              const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
              this.newEventDetail.attachInfoList.push({
                filePath: resFileInfo.data.body.filePath,
                attachTypeCode: FILE_TYPE.FILE,
                fileId: res.data.body.fileList[i].fileId
              })
            }
            if (res.data.body.fileList[i].keyName === "file2") {
              const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
              this.newEventDetail.attachInfoList.push({
                filePath: resFileInfo.data.body.filePath,
                attachTypeCode: FILE_TYPE.IMAGE,
                fileId: res.data.body.fileList[i].fileId
              })
            }
          }
        } else {
          return false
        }
      } else if (this.getFile !== null && this.getImage === null) {
        const res = await this.$file.uploadFile([this.getFile], 'ICM')
        if (res) {
          for (let i = 0; i < res.data.body.fileList.length; i++) {
            const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
            this.newEventDetail.attachInfoList.push({
              filePath: resFileInfo.data.body.filePath,
              attachTypeCode: FILE_TYPE.FILE,
              fileId: res.data.body.fileList[i].fileId
            })
          }
        } else {
          return false
        }
      } else if (this.getFile === null && this.getImage !== null) {
        const res = await this.$file.uploadFile([this.getImage], 'ICM')
        if (res) {
          for (let i = 0; i < res.data.body.fileList.length; i++) {
            const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
            this.newEventDetail.attachInfoList.push({
              filePath: resFileInfo.data.body.filePath,
              attachTypeCode: FILE_TYPE.IMAGE,
              fileId: res.data.body.fileList[i].fileId
            })
          }
        } else {
          return false
        }
      }
    },

        async assignValueToTab(contentsList) {
            for (let i = 0; i < contentsList.length; i++) {
                this.newEventDetailsLangDefaultValue.map((item) => {
                    if (item.languageCode === contentsList[i].languageCode) {
                        item.title = contentsList[i].title;
                        item.contentDesc = contentsList[i].contentDesc;
                        item.contentHtml = contentsList[i].contentHtml;
                    } else if (item.contentHtml === '') {
                        item.contentHtml = '<p><br/></p>'
                    }
                })
            }
        },

        assignValueToAttachInfoList(attachInfoList) {
            this.newEventDetail.attachInfoList = attachInfoList.map((item) => {
                return {
                    attachTypeCode: item.attachTypeCode,
                    fileId: item.fileId,
                    filePath: item.filePath
                }
            })
        },
    },
});
